<script>
// @ is an alias to /src

export default {
  name: "PicturePicture",
  data() {
    return {
      positive: "",
      negative: "",
      baseTab: 1,
      isBigHeight: true,
      similaritySlider: 20
    };
  },
  methods: {
    toggleArea() {
      this.isBigHeight = !this.isBigHeight;
