<script>
// @ is an alias to /src

export default {
  name: "TextPicture",
  data() {
    return {
      positive: "",
      negative: "",
      baseTab: 1,
      isBigHeight: true,
      flex: 1,
      picItems: [1, 2, 3, 4],
      sizeItems: [1, 2, 3, 4],
      currentPicItemsIndex: 0,
      currentSizeItemsIndex: 0,
      picNum: null,
      widthSlider: 0,
      heightSlider: 0,
      samplingSlider: 0,
      cfgSlider: 0,
      tcsSlider: 0,
      lang: 'en',
      lang1: 'en',
      langList: [
        { value: 'en', label: '英文'},
        { value: 'zh', label: '中文'}
      ]
    };
  },
  methods: {
    toggleLanguage(lang) {
      this.lang = lang
    },
    toggleLanguage1(lang) {
      this.lang1 = lang
    },
    picItemsActivate(index) {
      this.currentPicItemsIndex = index;
    },
    sizeItemsActivate(index) {
      this.currentSizeItemsIndex = index;
    },
    toggleArea() {
      this.isBigHeight = !this.isBigHeight;
    },
    setBaseTab(tab) {
      if (this.baseTab !== tab) {
        this.baseTab = tab;
        this.isBigHeight = false;
      } else {
        this.isBigHeight = !this.isBigHeight;
      }
    },
    setFlex(flex) {
      this.flex = flex;
    },
    widthInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.widthSlider = parsedValue;
      } else {
        this.widthSlider = 0;
      }
    },
    heightInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.heightSlider = parsedValue;
      } else {
        this.heightSlider = 0;
      }
    },
    samplingInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.samplingSlider = parsedValue;
      } else {
        this.samplingSlider = 0;
      }
    },
    cfgInput(value) {
      const parsedValue = parseFloat(value);
      if (!isNaN(parsedValue)) {
        this.cfgSlider = parsedValue;
      } else {
        this.cfgSlider = 0.0;
      }
    },
    tcsInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.tcsSlider = parsedValue;
      } else {
        this.tcsSlider = 0;
      }
    }
  }
};
</script>

