                <li :key="item.value" :class="[lang1 === item.value?'active':'']" @click="toggleLanguage1(item.value)" v-for="item in langList">{{ item.label }}</li>
              </ul>
              <i slot="reference"><template v-for="item in langList">
                <span v-if="item.value===lang1" :key="item.value">{{ item.label }}</span>
