</template>
<script>
// @ is an alias to /src
export default {
  name: "CanvasPicture",
  data() {
    return {
      positive: "",
      negative: "",
      baseTab: 1,
      isBigHeight: true,
      currTool: "",
      historyForPen: [],
      strokeStyle: "#000",
      strokeStyleForEraser: "red",
      lineWidth: 10,
      lineWidthForEraser: 10,
      colorList: [
        "#01C7FC",
        "#3A87FD",
        "#5E30EB",
        "#BE38F3",
        "#E63B7A",
        "#FE6250",
        "#FE8648",
        "#FEB43F",
        "#FECB3E",
        "#FFF76B",
        "#E4EF65",
        "#96D35F"
      ],
      currLine: [],
      picItems: [1, 2, 3, 4],
      sizeItems: [1, 2, 3, 4],
      currentPicItemsIndex: 0,
      currentSizeItemsIndex: 0,
      picNum: null,
      widthSlider: 200,
      heightSlider: 200,
      samplingSlider: 200,
      cfgSlider: 7.0,
      tcsSlider: 20,
      flex: 1,
      lang: 'en',
      lang1: 'en',
      langList: [
        { value: 'en', label: '英文'},
        { value: 'zh', label: '中文'}
      ],
      isShowColorList: false,
    }
  },
  computed: {
    showCanvasTip() {
      return this.currLine.length === 0;
    }
  },
  mounted() {
    this.initCanvasSize();
    this.canvas();
    window.addEventListener("resize", this.setCanvasSize, false);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.setCanvasSize, false);
    window.removeEventListener("mousedown", this.mouseHandler, false);
  },
  methods: {
    showColorList() {
      this.isShowColorList = !this.isShowColorList
    },
    toggleLanguage(lang) {
      this.lang = lang
    },
    toggleLanguage1(lang) {
      this.lang1 = lang
    },
    setStrokeStyleForEraser(strokeStyleForEraser) {
      this.strokeStyleForEraser = strokeStyleForEraser;
    },
    setStrokeStyle(strokeStyle) {
      this.strokeStyle = strokeStyle;
    },
    setLineWidth(lineWidth) {
      this.lineWidth = lineWidth;
    },
    setLineWidthForEraser(lineWidth) {
      this.lineWidthForEraser = lineWidth;
    },
    setCurrTool(currTool) {
      this.currTool = currTool;
    },
    clearCanvas(){
      this.historyForPen = [];
      this.clear()
    },
    clear() {
      const dpr = window.devicePixelRatio;
      let canvas = document.querySelector("canvas");
      let ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, canvas.width * dpr, canvas.height * dpr);
    },
    render() {
      const dpr = window.devicePixelRatio;
      let canvas = document.querySelector("canvas");
      let ctx = canvas.getContext("2d");
      for (let i = 0; i < this.historyForPen.length; i++) {
        let lineJson = this.historyForPen[i];
        if (i === 0) {
          this.clear();
        }
        ctx.beginPath();
        if (lineJson.type === 'pen') {
          for (let j = 0; j < lineJson.line.length; j++) {
            let dot = lineJson.line[j];
            if (j === 0) {
              ctx.lineWidth = dot[2] * dpr;
              ctx.strokeStyle = dot[3];
              ctx.moveTo(dot[0] * dpr, dot[1] * dpr);
            } else {
              ctx.lineTo(dot[0] * dpr, dot[1] * dpr);
              if (j === lineJson.line.length - 1) {
                ctx.stroke();
              }
            }
          }
        } else if (lineJson.type === 'eraser') {
          for (let i = 0; i < lineJson.line.length; i++) {
          let dot = lineJson.line[i];
          ctx.clearRect(
              (dot[0] - dot[2] / 2) * dpr,
              (dot[1] - dot[2] / 2) * dpr,
              dot[2] * dpr,
              dot[2] * dpr
            );
          }
        }
      }
    },
    prev() {
      if (this.historyForPen.length > 0) {
        if (this.historyForPen.length === 1) {
          this.clear();
        }
        this.historyForPen.pop();
        this.render();
      } 
    },
    initCanvasSize(cb) {
      const dpr = window.devicePixelRatio;
      let pictureSpace = document.querySelector(".picture-space-box");
      let canvas = document.querySelector("canvas");
      canvas.style.width = "";
      canvas.style.height = "";
      canvas.width = 0;
      canvas.height = 0;
      this.$nextTick(() => {
        let style = window.getComputedStyle(pictureSpace, null);
        canvas.width = parseFloat(style.width) * dpr;
        canvas.height = parseFloat(style.height) * dpr;
        canvas.style.width = style.width;
        canvas.style.height = style.height;
        typeof cb === "function" && cb();
      });
    },
    setCanvasSize() {
      this.initCanvasSize(() => {
        this.render();
      });
    },
    mouseHandler(e) {
      //设置边线颜色
      const dpr = window.devicePixelRatio;
      let canvas = document.querySelector("canvas");
      let ctx = canvas.getContext("2d");
      if (this.currTool === "pen") {
        ctx.strokeStyle = this.strokeStyle;
        ctx.lineWidth = this.lineWidth * dpr;
        this.drawLine(e);
      } else if (this.currTool === "eraser") {
        ctx.lineWidthForEraser = this.lineWidthForEraser * dpr;
        this.clearLine(e);
      }
    },
    clearLine(e) {
      const dpr = window.devicePixelRatio;
      let canvas = document.querySelector("canvas");
      var ctx = canvas.getContext("2d");
      if (e.type === "mousedown") {
        this.currLine = [];
        //设置一个清除矩形，左上角坐标为前面两个参数，
        //第三个参数是清除矩形的宽度，最后一个参数是清除矩形的高度，都是以像素为单位。
        //e.offsetX,e.offsetY 为当前鼠标按下的坐标
        ctx.clearRect(
          e.offsetX * dpr,
          e.offsetY * dpr,
          this.lineWidthForEraser * dpr,
          this.lineWidthForEraser * dpr
        );
        //起始点
        this.currLine.push([
          e.offsetX,
          e.offsetY,
          this.lineWidthForEraser
        ]);
        //添加两个事件监听
        canvas.addEventListener("mousemove", this.mouseHandler);
        canvas.addEventListener("mouseup", this.mouseHandler);
      } else if (e.type === "mousemove") {
        ctx.clearRect(
          e.offsetX * dpr,
          e.offsetY * dpr,
          this.lineWidthForEraser * dpr,
          this.lineWidthForEraser * dpr
        );
        this.currLine.push([
          e.offsetX,
          e.offsetY,
          this.lineWidthForEraser
        ]);
      } else if (e.type === "mouseup") {
        this.historyForPen.push({type: 'eraser', line: this.currLine});
        //鼠标抬起，清除监听功能
        canvas.removeEventListener("mousemove", this.mouseHandler);
        canvas.removeEventListener("mouseup", this.mouseHandler);
      }
    },
    drawLine(e) {
      const dpr = window.devicePixelRatio;
      let canvas = document.querySelector("canvas");
      let ctx = canvas.getContext("2d");
      if (e.type === "mousedown") {
        this.currLine = [];
        //起始一条路径，或者重绘当前路径
        ctx.beginPath();
        //起始点
        this.currLine.push([
          e.offsetX,
          e.offsetY,
          this.lineWidth,
          this.strokeStyle,
        ]);

        ctx.moveTo(e.offsetX * dpr, e.offsetY * dpr);

        canvas.addEventListener("mousemove", this.mouseHandler, false);
        canvas.addEventListener("mouseup", this.mouseHandler, false);
      } else if (e.type === "mousemove") {
        //终点
        ctx.lineTo(e.offsetX * dpr, e.offsetY * dpr);
        this.currLine.push([
          e.offsetX,
          e.offsetY,
          this.lineWidth,
          this.strokeStyle
        ]);

        ctx.lineJoin = "round";
        //开始绘制
        ctx.stroke();
      } else if (e.type === "mouseup") {
        this.historyForPen.push({type: 'pen', line: this.currLine});
        canvas.removeEventListener("mousemove", this.mouseHandler, false);
        canvas.removeEventListener("mouseup", this.mouseHandler, false);
      }
    },
    canvas() {
      let canvas = document.querySelector("canvas");
      //给画布添加一个鼠标按下的事件监听
      canvas.addEventListener("mousedown", this.mouseHandler, false);
    },
    setFlex(flex) {
      this.flex = flex;
    },
    toggleArea() {
      this.isBigHeight = !this.isBigHeight;
      this.setCanvasSize();
    },
    setBaseTab(tab) {
      if (this.baseTab !== tab) {
        this.baseTab = tab;
        this.isBigHeight = false;
      } else {
        this.isBigHeight = !this.isBigHeight;
      }
      this.setCanvasSize();
    },
    picItemsActivate(index) {
      this.currentPicItemsIndex = index;
    },
    sizeItemsActivate(index) {
      this.currentSizeItemsIndex = index;
    },
    widthInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.widthSlider = parsedValue;
      } else {
        this.widthSlider = 0;
      }
    },
    heightInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.heightSlider = parsedValue;
      } else {
        this.heightSlider = 0;
      }
    },
    samplingInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.samplingSlider = parsedValue;
      } else {
        this.samplingSlider = 0;
      }
    },
    cfgInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.cfgSlider = parsedValue;
      } else {
        this.cfgSlider = 0;
      }
    },
    tcsInput(value) {
      const parsedValue = parseInt(value, 10);
      if (!isNaN(parsedValue)) {
        this.tcsSlider = parsedValue;
      } else {
        this.tcsSlider = 0;
      }
    }
  }
};
