<template>
  <div class="generate">
    <div class="nav">
      <i :class="[activeId]"></i>
      <a
          v-for="item in tabs"
          :key="item.id"
          href="javascript:void(0)"
          @click="gotoRoute(`/generate?id=${item.id}`)"
          :class="activeId === item.id ? 'active' : ''"
      >
        <img v-show="activeId === item.id" :src="item.imgActive"/>
        <img v-show="activeId !== item.id" :src="item.img"/>
      </a>
    </div>
    <div class="workspace">
      <TextPicture v-if="activeId === tabs[0].id"></TextPicture>
      <picturePicture v-if="activeId === tabs[1].id"></picturePicture>
      <aiPicture v-if="activeId === tabs[2].id"></aiPicture>
      <canvasPicture v-if="activeId === tabs[3].id"></canvasPicture>
      <stylePicture v-if="activeId === tabs[4].id"></stylePicture>
    </div>
    <el-dialog :visible.sync="visible" class="func-dialog">
      <div class="func-mask">
        <h3>该功能暂未上线，敬请期待</h3>
        <a href="javascript:void(0)" @click="visible = false">Ok</a>
      </div>
    </el-dialog>
  </div>
</template>
<script>
// @ is an alias to /src
import TextPicture from "./components/TextPicture.vue";
import PicturePicture from "./components/picturePicture.vue";
import AiPicture from "./components/aiPicture.vue";
import CanvasPicture from "./components/canvasPicture.vue";
import StylePicture from "./components/stylePicture.vue";
import textPictureActiveImg from "@/assets/textPictureActive.png";
import textPictureImg from "@/assets/textPicture.png";
import picturePictureActiveImg from "@/assets/picturePictureActive.png";
import picturePictureImg from "@/assets/picturePicture.png";
import aiPictureActiveImg from "@/assets/aiPictureActive.png";
import aiPictureImg from "@/assets/aiPicture.png";
import canvasPictureActiveImg from "@/assets/canvasPictureActive.png";
import canvasPictureImg from "@/assets/canvasPicture.png";
import stylePictureActiveImg from "@/assets/stylePictureActive.png";
import stylePictureImg from "@/assets/stylePicture.png";

export default {
  name: "textToPicture",
  components: {
    TextPicture,
    PicturePicture,
    AiPicture,
    CanvasPicture,
    StylePicture
  },
  data() {
    return {
      tabs: [
        {
          id: "textPicture",
          img: textPictureImg,
          imgActive: textPictureActiveImg
        },
        {
          id: "picturePicture",
          img: picturePictureImg,
          imgActive: picturePictureActiveImg
        },
        {
          id: "aiPicture",
          img: aiPictureImg,
          imgActive: aiPictureActiveImg
        },
        {
          id: "canvasPicture",
          img: canvasPictureImg,
          imgActive: canvasPictureActiveImg
        },
        {
          id: "stylePicture",
          img: stylePictureImg,
          imgActive: stylePictureActiveImg
        }
      ],
      visible: false
    };
  },
  computed: {
    activeId() {
      const id = this.$route.query.id;
      let isExist = false;
      for (let item of this.tabs) {
        if (item.id === id) {
          isExist = true;
          break;
        }
      }
      if (!id || !isExist) {
        return this.tabs[0].id;
      }
      return id;
    }
  },
  methods: {
    gotoRoute(path) {
      if (path.includes('stylePicture')) {
        this.visible = true;
      } else {
        this.$router.push({
          path: path
        });
      }
    }
  }
};
</script>
